
.reviews {
  color: #fff;
  padding-bottom: 80px;
}

.slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;

  li button {
    display: block;
    width: 10px;
    height: 10px;
    padding: 0;
    margin: 0 4px;
    border-radius: 50%;
    background-color: #FF5353;
    opacity: 0.3;
    color: rgba(255,255,255,0);
  }

  li {
    &.slick-active button {
      opacity: 1;
    }
  }
}

.reviews-content {
  position: relative;

  background: linear-gradient(208.18deg, #FF5353 9.05%, #FF7F7F 76.74%);
  border-radius: 24px;
  padding: 40px 110px 40px;

  .slick-dots {
    position: absolute;
    bottom: -100px;
  }

  &:before {
    position: absolute;
    top: 0;
    right: -40px;

    content: "";

    width: 130px;
    height: 115px;

    background: url("../images/dots.png") center no-repeat;
    background-size: contain;
  }

  &:after {
    position: absolute;
    bottom: -45px;
    left: -80px;

    content: "";

    width: 130px;
    height: 115px;

    background: url("../images/dots-2.png") center no-repeat;
    background-size: contain;

    z-index: -10;
  }
}

.usage-title {
  max-width: 915px;
  margin-left: auto;
  margin-right: auto;
}

.reviews-title {
  position: relative;

  font-size: 48px;
  line-height: 1.5;
  color: #fff;

  &:before {
    position: absolute;
    bottom: -10px;
    left: calc(50% - 28px);

    content :"";

    width: 56px;
    height: 2px;

    border-radius: 6px;
    background-color: #fff;
  }

  span {
    color: #fff;
  }
}

.user-info {
  display: flex;
  align-items: center;

  width: 39%;
}

.user-photo {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  background-color: #fff;
  margin-right: 30px;
  overflow: hidden;

  img {
    display: block;
    width: 100%;
  }
}

.user-name {
  font-size: 22px;
  font-weight: 500;

}

.slick-dots {
  width: 100%;
}

.slick-dots li {
  position: relative;
  z-index: 20;
}

.review {
  display: flex !important;
  justify-content: space-between;
  align-items: center;

  width: 850px !important;
  margin-left: auto;
  margin-right: auto;
  min-height: 200px;

  .review-inner {
    width: 59%;
  }
}

.reviews-btns {
  position: absolute;
  left: calc(50% - 165px);
  bottom: -65px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 330px;
}

.review-btn {
  width: 30px;
  height: 20px;

  background: url("../images/arrow-2.png") center no-repeat;
  background-size: contain;

  &.prev {
    transform: scale(-1,1);
  }
}

