  }

}

@media (max-width: 991px) {

  .reviews {
    padding-bottom: 60px;
  }

  .reviews-content .slick-dots {
    bottom: -80px;
  }

  .reviews-title {
    font-size: 36px;
  }

  .review {
    width: 650px !important;
  }

  .reviews-btns {
    bottom: -45px;
  }

  .user-info {
    width: 35%;
  }

  .user-photo {
    width: 100px;
    height: 100px;
    margin-right: 20px;
  }

  .review .review-inner {
    width: 63%;
    font-size: 16px;
  }

}

