@media (max-width: 767px) and (min-width: 586px) {
  .review .review-inner {
    font-size: 18px;
  }
}

@media (max-width: 585px) {

  .review .review-inner {
    min-height: 200px;
  }

  .reviews-content:after {
    bottom: 25px;
    left: -54px;
    z-index: 10;
  }


}

@media (max-width: 480px) {

  .reviews-title {
    font-size: 23px;
  }

  .review .review-inner {
    min-height: initial;
  }

  .reviews-btns {
    left: 0;
    width: 100%;
  }

  .user-photo {
    width: 70px;
    height: 70px;
  }

}

