.footer {
  position: relative;
  padding-top: 50px;
  padding-bottom: 30px;

  &:before {
    position: absolute;
    bottom: 0;
    left: calc(50% - 960px);
    content: "";

    width: 1195px;
    height: 740px;

    background: url("../images/flowers-2.png") center bottom no-repeat;
    background-size: contain;
    pointer-events: none;
    z-index: -10;
  }

  &:after {
    position: absolute;
    bottom: 0;
    right: calc(50% - 910px);

    content: "";

    width: 726px;
    height: 400px;

    background: url("../images/eye.png") center no-repeat;
    background-size: contain;
    pointer-events: none;
    z-index: -10;
  }
}

.footer-info-wrapper {
  background: linear-gradient(208.18deg, #FF5353 9.05%, #FF7F7F 76.74%);
  padding-top: 10px;
  padding-bottom: 10px;
}

.footer-info {
  color: #fff;
  font-size: 14px;
  text-align: center;

  li {
    margin-bottom: 5px;
  }
}

.popup {
  background-color: #fff;
  border: 2px solid #FF5353;
  padding: 15px;

  h2 {
    text-align: center;
    margin-top: 10px;
  }

  p {
    margin-bottom: 10px;
    line-height: 1.2;
    font-size: 16px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

@media (min-width: 1920px) {
  .footer:before {
    left: 0;
  }

}


@media (min-width: 1800px) {
  .footer:before {
    right: 0;
  }
}




.main-headline {
  //font-size: 64px;
  color: #FF5353;
  text-align: right;

  .text {
    font-size: 64px;
  }

  h2 {
    margin: 0;
    font-size: 64px;
    font-weight: normal;
  }
}

.footer-head {
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
  align-items: center;

  font-family: Atma, sans-serif;
}

.footer-subtitle {
  width: 400px;
  font-size: 30px;
}

.footer-content {
  position: relative;

  &:before {
    position: absolute;
    top: 70px;
    right: -310px;

    content: "";

    width: 232px;
    height: 221px;

    background: url("../images/flowers-3.png") center no-repeat;
    background-size: contain;
  }
}



.footer-images {
  position: absolute;
  bottom: -30px;
  right: 0;

  width: 100%;
  //width: ;

  .image {
    position: absolute;
    bottom: 0;

    img {
      display: block;
      width: 100%;
    }
  }

  .images-1 {
    right: 65px;
    bottom: 70px;
    width: 154px;
    z-index: 50;
  }

  .images-2 {
    right: 0;
    //width: 91px;
    bottom: 132px;
    z-index: 40;
  }

  .images-3 {
    right: 130px;
    bottom: 40px;
    width: 150px;

    &:before {
      position: absolute;
      top: 10px;
      right: 20px;

      content: "";

      width: 91px;
      height: 100px;

      background: url("../images/prod-1.png") center bottom no-repeat;
      background-size: contain;
    }

    &:after {
      position: absolute;
      top: calc(50% - 20px);
      left: -53px;

      content: "";

      width: 40px;
      height: 40px;

      background: url("../images/icon-plus.png") center no-repeat;
      background-size: contain;
    }
  }

  .images-4 {
    left: 260px;
    width: 522px;
  }
}

@media (max-width: 1799px) {
  .footer-content:before {
    top: -160px;
    right: -200px;
  }
}

@media (max-width: 1536px) {
  .footer:after {
    right: 0;

    width: 676px;
    height: 250px;
    background-position: right bottom;
  }
}

@media (max-width: 1365px) {

  .footer-content:before {
    top: 160px;
    right: 290px;
  }

  .footer:before {
    width: 995px;
    height: 540px;
  }

  .footer-images .images-1 {
    right: 100px;
  }

  .footer-images .images-3 {
    right: 210px;
  }

  .footer-images .images-2 {
    right: 40px;
  }

  .footer-images .images-4 {
    width: 442px;
  }
}

@media (max-width: 1199px) {

  .footer:after {
    background: none;
  }

  .footer-content:before {
    right: 210px;
  }

  .footer {
    .main-headline h2 {
      font-size: 46px;
    }

    .main-headline .text {
      font-size: 46px;
    }
  }

  .footer-images .images-1 {
    right: 90px;
    bottom: 160px;
    width: 140px;
  }

  .footer-images .images-2 {
    right: 0;
    bottom: 182px;
  }

  .footer-images .images-3 {
    right: 30px;
    bottom: 20px;
  }

}

@media (max-width: 991px) {

  .footer {
    padding-bottom: 50px;
  }

  .footer:before {
    background: none;
  }

  .footer-images .images-3 {
    right: 90px;
  }

  .footer-images .images-2 {
    right: 0;
  }

  .footer-content:before {
    right: 200px;
  }

  .footer-images {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;

    margin-top: 40px;
  }

  .footer-images .images-3 {
    right: 0;
  }

  .footer-images .images-1 {
    right: auto;
    bottom: auto;
    left: 0;
    top: 50px;
  }

  .footer-images .images-2 {
    right: auto;
    left: 80px;
    bottom: 0;
    top: 60px;
  }

  .footer-images .images-4 {
    position: static;
    //width: 100%;
    //text-align: center;
    margin-left: auto;
    margin-right: auto;
  }

  .footer-content:before {
    right: 0;
  }

  .footer .order {
    margin-top: -20px;
  }

  .footer-head {
    display: block;
  }

  .footer .main-headline {
    text-align: center;
  }

  .footer-subtitle {
    width: 100%;
    text-align: center;
  }

}

@media (max-width: 767px) {

  .footer-images .images-4 {
    width: 362px;
  }



}


@media (max-width: 585px) {

  .footer-images .images-4 {
    width: 332px;
  }

  .footer-images .images-1 {
    width: 124px;
  }

  .footer-images .images-2 {
    left: 64px;
    bottom: 0;
    top: 47px;
    width: 100px;
  }

  .footer-images .images-3 {
    bottom: 0;
    z-index: 30;
  }
}


@media (max-width: 480px) {

  .footer-content:before {
    background: none;
    display: none;
  }

  .footer-images {
    margin-top: 20px;
  }

  .footer .order {
    margin-top: 0;
    padding-top: 40px;
  }

  .footer-images .images-1 {
    bottom: -8vw;
    left: -5px;
    top: auto;
    width: 33vw;
    max-width: 133px;
  }

  .footer-images .images-2 {
    right: auto;
    left: 45vw;
    bottom: -8vw;
    top: auto;
    width: 30vw;
    max-width: 133px;
    transform: rotate(8deg);

  }

  .footer-images .images-4 {
    width: 80vw;
    max-width: 355px;

  }

  .footer-images .images-3 {
    right: -17px;

    width: 26vw;
    max-width: 115px;
    bottom: -8vw;
  }

  .footer-images .images-3:before {
    display: none;
    background: none;

    bottom: 0;
    z-index: 30;
  }

  .footer-images .images-3:after {
    left: -8vw;
    width: 6.6vw;
  }
}

.footer-info-wrapper {
  display: none;
}

.show-tiktok {
  display: block !important;
}

@media (max-width: 480px) {

  .footer .main-headline .text,
  .footer .main-headline h2 {
    font-size: 32px;
  }

  .footer-subtitle {
    font-size: 18px;
  }

}

@media (max-width: 320px) {

}