.header {
  position: relative;
  padding-top: 10px;
  font-family: Atma, sans-serif;

  &:before {
    position: absolute;
    top: -60px;
    left: calc(50% - 1130px);

    content: "";

    width: 844px;
    height: 705px;
    background: url("../images/header-flowers.png") center no-repeat;
    background-size: contain;
    pointer-events: none;
  }

  &:after {
    position: absolute;
    top: -30px;
    right: calc(50% - 1050px);

    content: "";

    width: 844px;
    height: 705px;
    background: url("../images/header-flowers.png") center no-repeat;
    background-size: contain;
    transform: scale(1, -1);
    background-size: contain;
    pointer-events: none;
  }
}

@media (min-width: 1920px) {
 .header:before {
   left: -170px;
 }
.header:after {
  right: -90px;
}
}

.header-title {
  //font-weight: ;
  color: #FF5353;
  text-align: center;

}

.header-title .head {
  font-size: 64px;
}

h1 {
  font-size: 70px;
  font-weight: normal;
  margin-top: 0;
  margin-bottom: 0;

}

.title {

}

.header-subtitle {
  text-align: center;
  font-size: 30px;
  margin-top: 10px;
}

.complementary-text {
  color: #FF5353;
  margin-top: 15px;
  text-align: center;
}

.header-content {
  position: relative;
  max-width: 550px;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 320px;

  &:before {
    position: absolute;
    top: -100px;
    left: calc(50% - -300px);
    content: "";
    width: 575px;
    height: 592px;

    background: url("../images/prod-1.png") center top no-repeat;
    background-size: contain;
    pointer-events: none;
    z-index: 10;
  }
}

.header-items {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  text-align: center;

  margin-top: 30px;
  margin-bottom: 60px;

  .item {
    font-size: 14px;
    width: 31%;
    font-family: Roboto, sans-serif;

  }
}

.link-style {
  display: block;
  width: 100%;
  max-width: 200px;
  text-align: center;
  color: #fff;font-size: 30px;
  background-color: rgb(83, 172, 255);
  border-radius: 30px;
  padding: 10px 5px;
  text-transform: uppercase;
  font-weight: bold;
  line-height: 1.1;
  box-shadow: 0px 10px 10px rgba(83, 172, 255, 0.3);
  margin-left: auto;
  margin-right: auto;
  transition: 320ms;

  &:hover {
    box-shadow: 0px 0 15px rgba(83, 172, 255, 0.5);
  }
}

.fixed-order-link {
  position: fixed;
  right: 20px;
  bottom: 20px;

  width: 55px;
  height: 55px;


  background: rgb(83, 172, 255) url("../images/basket.png") center no-repeat;
  background-size: 60%;
  border-radius: 50%;

  box-shadow: 0px 10px 10px rgba(83, 172, 255, 0.3);
  transition: 320ms;

  z-index: 200;
  pointer-events: none;
  opacity: 0;

  &.active {
    pointer-events: auto;
    opacity: 1;
  }

  &:hover {
    box-shadow: 0px 0 15px rgba(83, 172, 255, 0.5);
  }
}

@media (max-width: 585px) {
  .fixed-order-link {
    width: 40px;
    height: 40px;
    right: 10px;
  }
}

.header-images {
  position: absolute;
  left: -460px;
  bottom: 60px;
  //padding-left: calc(313px + 40px);

  width: 730px;

  &:before {
    position: absolute;
    bottom: 20px;
    left: 60px;
    content: "";
    width: 445px;
    height: 686px;

    background: url("../images/girl.png") center no-repeat;
    background-size: contain;
  }

  .image {
    img {
      display: block;
      width: 100%;
    }
  }

  .image-1 {
    position: absolute;
    bottom: 20px;
    right: 130px;
    width: 210px;

    z-index: 20;
  }

  .image-2 {
    position: absolute;
    right: 240px;
    bottom: 0;
    width: 135px;

    &:before {
      position: absolute;
      top: calc(50% - 20px);
      left: -40px;

      content: "";
      width: 40px;
      height: 40px;
      background: url("../images/icon-plus.png") center no-repeat;
      background-size: contain;
    }
  }

  .image-3 {
    //position: relative;
    z-index: 20;
    position: absolute;
    bottom: -30px;
    left: 0;
    overflow: hidden;
    border-radius: 50%;
    border: 3px solid #FF5353;
    width: 313px;
  }

  .sale-text {
    position: absolute;
    bottom: 60px;
  }
}


.sale-text {
  position: relative;
  font-size: 48px;

  color: #fff;
  //font-family: Roboto, sans-serif;
  font-weight: bold;
  right: 0;

  width: 200px;
  line-height: 100px;
  text-align: center;

  span {
    position: relative;
    z-index: 20;
  }

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    content: "";

    width: 100%;
    height: 100%;
    background: radial-gradient(75.33% 75.33% at 50% 50%, #FF5353 90%, rgba(255, 83, 83, 0) 100%);
    opacity: 0.7;
    filter: blur(30px);
  }

}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .sale-text:before {
    top: -15%;
    left: -15%;

    width: 130%;
    height: 130%;
    opacity: 0.9;
    background: radial-gradient(75.33% 75.33% at 50% 50%, rgba(255, 86, 86, 0.5) 18.75%, rgba(255, 73, 73, 0) 60.42%, rgba(255, 75, 75, 0) 65.62%);
  }
}

.header .timer {
  position: absolute;
  bottom: 150px;
  right: -292px;
}

.header-video {
  position: absolute;
  bottom: 0;
  right: -292px;
}

.header-video {
  display: flex;
  justify-content: space-between;
  align-items: center;

  width: 395px;
  margin-top: 20px;

  .text {
    color: #FF5353;
    text-align: center;
    width: calc(100% - 160px - 20px);
    margin-left: 20px;

    span {
      display: block;
      color: #555;
    }
  }

  .video-poster {
    position: relative;

    width: 160px;
    border: 11px solid #fff;
    border-radius: 20px;
    overflow: hidden;
    cursor: pointer;

    img {
      display: block;
      width: 100%;
    }

    &:before {
      position: absolute;
      top: calc(50% - 18px);
      left: calc(50% - 18px);

      content: "";

      width: 36px;
      height: 36px;

      background: url("../images/play-icon.png") center no-repeat;
      background-size: contain;
      z-index: 10;
      pointer-events: none;
      transition: 320ms;
      transform: rotate(0.0001deg) scale(0.8);
    }

    &:hover:before {
      transform: rotate(0.0001deg) scale(1);
    }
  }
}

