.about {
  padding-top: 30px;
  padding-bottom: 40px;
}

.about-content {
  position: relative;

  display: flex;
  justify-content: center;
  align-items: flex-end;

  margin-top: 50px;

  &:before {
    position: absolute;
    top: calc(50% - 337px + 30px);
    left: calc(50% - 462px);

    content: "";

    width: 924px;
    height: 675px;

    background: url("../images/flawers.png") center no-repeat;
    background-size: contain;

    z-index: -20;
  }

  .item {
    position: relative;

    padding: 100px 20px 20px;
    border-radius: 20px;
    background-color: #fff;
    margin-top: 0;
    margin-bottom: 60px;
    text-align: center;

    &:last-child {
      margin-bottom: 0;
    }

    .icon {
      position: absolute;
      top: -20px;
      left: 0;

      width: 100%;
      height: 100px;
    }
  }
}

.about .product-image {
  width: 34%;

  img {
    display: block;
    width: 100%;
  }
}

.about .items-wrapper {
  width: 31%;
}

.about h4 {
  font-weight: 500;
}

.about .item p {
  display: flex;
  flex-direction: column;
  justify-content: center;

  min-height: 70px;
  font-size: 16px;
}

.about .item {
  box-shadow: 0px 0px 20px rgba(255, 83, 83, 0.2);
}

