}

@media (max-width: 1365px) {

}

@media (max-width: 991px) {
  .about-content {
    display: block;
  }

  .about .items-wrapper {
    width: 100%;
  }
}

@media (max-width: 991px) and (min-width: 586px) {

  .about .items-wrapper {
    display: flex;
    justify-content: space-between;
  }

  .about-content .item {
    width: 48%;
    margin-bottom: 20px;
  }

  .about .product-image {
    width: 50%;
    margin-left: auto;
    margin-right: auto;
  }

  .about-content:before {
    top: calc(50% - 387px + 30px);
  }

  .about-content .item:last-child {
    margin-bottom: 20px;
  }




  .about-content:before {
    top: calc(50% - 277px + 30px);
    left: calc(50% - 312px);

    width: 624px;
    height: 455px;
  }

  .about h4 {
    font-size: 20px;
  }

  .about .item {
    padding-top: 80px;
  }

  .about .item p {
    margin-top: 10px;
  }

  .about-content .item .icon {
    height: 80px;

    img {
      height: 100%;
    }
  }

  .about .product-image {
    margin-bottom: 20px;
  }

}

@media (max-width: 767px) {

  .about .product-image {
    width: 79vw;
    height: auto;
    margin: 30px auto;
    max-width: 350px;
  }

  h4 {
    font-size: 20px;
  }

}

@media (max-width: 585px) {

  .about-content:before {
    background-image: url("../images/flawers-480.png");
    top: calc(50% - 77vw + 30px);
    left: -15px;
    width: 100vw;
    height: 135vw;
  }

  .about-content .item {
    padding-top: 90px;
    margin-bottom: 40px;
  }

}

