* {
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 18px;
  color: #555555;

  background: #3C3C3C url("../images/back.jpg") center no-repeat;
  background-size: cover;


  min-height: 5229px;
}

@media (max-width: 991px) {
  body {
    background-image: url("../images/back-992.jpg");
  }
}

@media (max-width: 767px) {
  body {
    background-image: url("../images/back-768.jpg");
  }
}

@media (max-width: 480px) {
  body {
    background-image: url("../images/back-mob.jpg");
  }
}


// - font-face --------------------------------------

a {
  text-decoration: none;
  color: inherit;
  font-family: inherit;
  border: none;
  outline: none;

  &:active {
    outline: none;
    border: none;
  }
}

button {
  font-family: inherit;
  color: inherit;
  outline: none;
  border: none;
  background: none;
  cursor: pointer;
}

input {
  font-family: inherit;
  color: inherit;
  background: none;
  border: none;
  outline: none;
  -webkit-appearance: none;

  &::placeholder {
    opacity: 1;
  }

  &::placeholder {
    color: inherit;
    opacity: 1;
  }

  &:invalid {
    box-shadow: none;
  }
}

img {
  user-select: none;

  -moz-user-select: none;
  -ms-user-select: none !important;
  -webkit-user-select: none !important;
}

select::-ms-expand {
  display: none
}

label.error {
  display: none !important;
}

input::-ms-clear {
  width: 15px;
  height: 15px;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.overflow-hidden {
  position: relative;
  overflow: hidden;
}

.back {
  position: absolute;
  top: 0;
  left: 50%;
  width: 1px;
  height: 100%;
  background: none;

  div {
    position: absolute;
  }

  img {
    position: absolute;
  }
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: auto;
  padding: 0 30px;
}

//.container {
//  padding-left: calc(15px + env(safe-area-inset-left));
//  padding-right: calc(15px + env(safe-area-inset-right));
//}

.overflow {
  position: relative;
  overflow: hidden;
}

p {
  margin: 0;
}

.full {
  position: absolute;

  top: 0;
  left: 0;

  width: 100%;
  z-index: 50000;
  pointer-events: none;
  opacity: 0.4;
}

.timer {
  position: relative;
  display: flex;
  justify-content: space-between;

  width: 430px;
  color: #53ACFF;

  z-index: 10;

  .timer-item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-family: Atma, sans-serif;
    text-align: center;
    //background: url("../images/round.png") center no-repeat;
    //background-size: 100%;

    width: 85px;
    height: 85px;
    //border-radius: 50%;
    //border: 4px solid #53ACFF;

    svg {
      position: absolute;
      top: 0;
      left: 0;

      pointer-events: none;
      width: 100%;
      height: 100%;
      fill: none;
      stroke: #53ACFF;
      stroke-width: 6px;
      stroke-linecap: round;
    }
  }

  .inner {
    position: relative;
    z-index: 20;
    display: block;
    font-weight: 500;
    font-size: 36px;
  }

  .text {
    position: relative;
    z-index: 20;
    display: block;
    font-size: 13px;
  }
}

svg {
  //width: 85px;
  //height: 85px;
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

.timer-circle {
  stroke-dashoffset: 0;
  stroke-dasharray: 0;
}