.title {
  font-family: Atma, sans-serif;
  font-size: 48px;
  margin: 0 0 30px;
  font-weight: normal;
  text-align: center;

  span {
    color: #FF5353;
  }
}

.video-title {
  font-weight: 500;

  span {
    font-weight: normal;
  }
}

.video {
  padding-top: 30px;
  padding-bottom: 30px;
  font-family: Atma, sans-serif;
}

.video-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

h4 {
  font-family: Atma;
  font-weight: 500;
  line-height: 1.5;
  font-size: 24px;
  margin-top: 0;
  margin-bottom: 0;
}

.video-description {
  text-align: right;
  width: 48%;

  .text-1 {
    font-family: Roboto, sans-serif;
    margin-bottom: 20px;
    margin-top: 10px;
    font-size: 16px;
  }

  h4 {
    font-weight: 500;
  }

  .text-2 {
    color: #FF5353;
    font-size: 24px;
    line-height: 1.5;
    margin-bottom: 20px;
    font-weight: 500;
  }

  .link-style {
    margin-right: 0;
  }
}

.video-inner {
  width: 50%;
}



.video-inner {
  position: relative;
  //width: 529px;
  cursor: pointer;

  img {
    display: block;
    //height: 100%;
    width: 100%;
  }

  iframe {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
  }


  &.inactive .video-image:before {
    display: none;
  }
}

.video-image {
  position: relative;
  height: 100%;

  &:before {
    position: absolute;
    top: calc(50% - 16px);
    left: calc(50% - 16px);
    content: "";

    width: 35px;
    height: 35px;
    background: url("../images/play-icon.png") center no-repeat;
    background-size: contain;

    z-index: 10;
    pointer-events: none;
    transform: scale(0.9);
    transition: 320ms;
  }

  &:hover {
    &:before {
      opacity: 1;
      transform: scale(1.1);
    }
  }
}

