@media (max-width: 1199px) and (min-width: 992px) {
  .video-content {
    position: relative;
    padding-bottom: 70px;
  }

  .video-description .link-style {
    position: absolute;
    bottom: 0;
    left: calc(50% - 100px);
  }
}

@media (max-width: 991px) {

  .video {
    padding-top: 10px;
  }

  .video-image {
    max-width: 580px;
    margin-left: auto;
    margin-right: auto;
  }

  .video-content {
    display: block;
  }

  .video-inner {
    width: 100%;
    margin-bottom: 30px;
  }

  .video-description {
    width: 100%;
    text-align: center;
  }

  .video-description .link-style {
    margin-right: auto;
  }



}

@media (max-width: 767px) {

}

@media (max-width: 585px) {

  .title {
    font-size: 32px;
  }

}

@media (max-width: 480px) {


  .title {
    font-size: 24px;
  }

  .video-title {
    span {
      display: block;
    }
  }

  .video-inner {
    margin-bottom: 20px;
  }

  .video-description .text-1 {
    margin-bottom: 15px;
  }

  .video-description .text-2 {
    margin-bottom: 5px;
    font-size: 20px;
  }

}

