.usage {
  position: relative;
  padding-top: 50px;
  padding-bottom: 50px;

  &:before {
    position: absolute;
    top: -720px;

    right: calc(50% - 960px);
    content: "";

    width: 834px;
    height: 1779px;

    background: url("../images/flowers-4.png") center right no-repeat;
    background-size: contain;
    pointer-events: none;
  }
}

@media (min-width: 1920px) {
  .usage:before {
    right: 0;
  }
}

.usage-content {
  display: flex;
  justify-content: space-between;

  max-width: 915px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 120px;
  padding-bottom: 120px;

  .step {
    position: relative;
    width: 29%;
    text-align: center;

    .text {
      margin-top: 15px;
      line-height: 30px;
      font-family: Atma, sans-serif;
    }

    .image {
      width: 100%;
      max-width: 177px;
      border-radius: 50%;
      overflow: hidden;
      margin-left: auto;
      margin-right: auto;

      img {
        display: block;
        width: 100%;
      }
    }

    &:nth-child(1):before {
      position: absolute;
      left: 50%;
      top: -100px;

      content: "";

      width: 293px;
      height: 82px;

      background: url("../images/arrow.png") center no-repeat;
      background-size: contain;
    }

    &:nth-child(2):before {
      position: absolute;
      left: 50%;
      bottom: -100px;

      content: "";

      width: 293px;
      height: 82px;

      transform: scale(1,-1);

      background: url("../images/arrow.png") center no-repeat;
      background-size: contain;
    }
  }
}

@media (max-width: 1536px) {

  .usage:before {
    top: -450px;
    right: calc(50% - 790px);
    width: 734px;
    height: 1479px;
  }

}

@media (max-width: 1365px) {

}

@media (max-width: 1199px) {

  .usage:before {
    right: calc(50% - 690px);
  }

}

@media (max-width: 991px) {

  .usage:before {
    background: none;
    display: none;
  }

  .usage {
    padding-top: 30px;
    padding-bottom: 30px;
  }

}


@media (max-width: 991px) and (min-width: 768px) {
  .usage-content .step .text {
    font-size: 18px;
  }

  .usage-content .step {
    width: 32%;
  }
}

@media (max-width: 767px) {

  .usage {
    padding-top: 20px;
    padding-bottom: 20px;
  }

  .usage-content {
    flex-wrap: wrap;
    justify-content: space-around;
    padding-bottom: 0;
  }

  .usage-content {
  }

  .usage-content .step {
    width: 48%;
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .usage-content {
    padding-top: 80px;
  }

  .usage-content .step:nth-child(2):before {
    transform: scale(1, 1) rotate(115deg);

    left: 51%;
    bottom: -148px;

    width: 183px;
    height: 82px;
  }

  .usage-content .step:nth-child(1):before {
    left: 64%;
    top: -90px;

    width: 193px;
    height: 82px;
  }

}

@media (max-width: 585px) {

  .usage-content .step:nth-child(2):before {
    left: 43%;
  }

}

@media (max-width: 480px) {

  .usage-content {
    display: block;
    padding-top: 0;
  }

  .usage-content .step {
    margin-left: auto;
    margin-right: auto;

    width: 100%;
    max-width: 260px;
  }

  .usage-content .step:nth-child(1):before {
    left: auto;
    right: -26vw;
    top: auto;
    width: 170px;
    bottom: 0;
    transform: rotate(90deg);
    background-image: url("../images/arrow-mob.png");
    //background-size: contain;
  }

  .usage-content .step:nth-child(2):before {
    left: -26vw;
    bottom: 0;

    width: 170px;

    transform: scale(-1, 1) rotate(90deg);
    background-image: url("../images/arrow-mob.png");
    //background-size: contain;
  }

}

@media (max-width: 320px) {

}