.product-card {
  padding-top: 60px;
  padding-bottom: 25px;

}

.product-card-content {
  position: relative;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 160px;

  &:before {
    position: absolute;
    right: 20px;
    bottom: 120px;

    content: "";

    width: 474px;
    height: 535px;

    background: url("../images/girl-3.png") center bottom no-repeat;
    background-size: contain;
  }
}

.prices {
  font-family: Atma, sans-serif;

}

.product-card .product {
  position: relative;

  .sale-text {
    position: absolute;
    right: -20px;
    bottom: 150px;
    font-family: Atma;
  }

  .product-image {
    width: 407px;

    img {
      display: block;
      width: 100%;
    }
  }

  .eye-image {
    position: absolute;
    top: 50px;
    left: -150px;

    width: 246px;
    height: 246px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid #FF5353;

    img {
      display: block;
      width: 100%;
    }
  }
}

.old-price-wrapper {
  display: flex;
  justify-content: space-between;
}

.old-price {
  display: inline;
  position: relative;
  font-size: 18px;

  &:before {
    position: absolute;
    top: calc(50% - 2px);
    left: -10px;
    content: "";

    width: calc(100% + 20px);
    height: 2px;
    background-color: #FF5353;

  }
}

.new-price-wrapper {
  font-size: 24px;
  text-align: center;

  .text {
    font-size: 24px;
    text-transform: uppercase;
    margin: 20px 0 15px;
  }

  .new-price {
    color: #FF5353;
  }
}

.order {
  position: relative;

  width: 320px;
  background-color: #FFF2F2;
  border-radius: 20px;
  padding: 30px;

  z-index: 20;

  box-shadow: 0 0 30px rgba(255, 83, 83, 0.4);
}

.new-price {
  font-size: 36px;
  text-transform: uppercase;
}

.timer--mini {
  width: 260px;

  .timer-item {
    width: 56px;
    height: 56px;

    .inner {
      font-size: 20px;
    }

    .text {
      font-size: 10px;
    }
  }
}


.order .timer--mini {
  margin-top: 20px;
}

.form-control {
  display: block;
  width: 100%;

  background-color: #fff;
  height: 50px;
  border-radius: 25px;
  padding: 0 10px 0 20px;
  box-shadow: inset 0px 0px 8px rgba(255, 186, 186, 0.6);
  margin-bottom: 15px;
}

.order-form {
  margin-top: 20px;
}

.product-card-promo {
  position: absolute;
  left: 0;
  bottom: 0;

  width: 100%;
  padding: 50px;

  color: #fff;
  border-radius: 20px;
  background: linear-gradient(34.59deg, #FF5353 10.24%, #FF8D8D 60.54%);

  .text {
    width: 70%;
    font-family: Atma, sans-serif;

    span {
      font-weight: 500;
    }
  }

  &:before {
    position: absolute;
    top: 0;
    right: 25%;
    content: "";

    height: 100%;
    width: 207px;
    background: url("../images/prod-6.png") center no-repeat;
    background-size: contain;
  }

  &:after {
    position: absolute;
    top: -110px;
    right: 6%;
    content: "";

    height: 238px;
    width: 215px;

    background: url("../images/present-box.png") center no-repeat;
    background-size: contain;
  }
}

@media (max-width: 1536px) {
  .product-card .product .eye-image {
    top: 110px;

    width: 206px;
    height: 206px;
  }
}

@media (max-width: 1365px) {

  .product-card .product .product-image {
    width: 350px;
    margin-left: 100px;
  }

  .product-card-content:before {
    width: 404px;
    height: 535px;
  }

  .product-card .product .eye-image {
    left: 0;
  }

  .product-card-content {
    align-items: flex-end;
  }

  .product-card .product .sale-text {

  }

}

@media (max-width: 1199px) {

  .product-card .product .eye-image {
    left: -15px;
    width: 136px;
    height: 136px;
  }

  .product-card .product .product-image {
    width: 320px;
    margin-left: 0;
  }

  .product-card .product .sale-text {
    top: -80px;
    bottom: auto;
  }



  .product-card-promo {
    padding-left: 30px;
    padding-right: 30px;
  }

  .product-card-promo:after {
    right: 2%;
  }

  .product-card-promo:before {
    right: 19%;
  }

}

@media (max-width: 991px) {

  .product-card-promo {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
  }

  .product-card-content {
    flex-wrap: wrap;
  }

  .product-card-promo {
    order: 2;

  }

  .product-card .product {
    order: 1;
  }

  .order {
    order: 3;
    margin-top: 30px;
    box-shadow: 0px 0px 30px rgba(255, 83, 83, 0.2);
  }

  .product-card-promo:after {
    background: none;
  }

  .product-card-content:before {
    right: 20px;
    bottom: auto;
    top: 0;

    width: 364px;
    height: 405px;
  }

  .product-card .product .product-image {

    margin-left: 60px;
  }

  .product-card .product .sale-text {
    top: 160px;
    right: -50px;
  }

  .product-card-promo:before {
    right: 2%;
    background-position: top right;
  }

  .product-card-content {
    padding-bottom: 0;
  }

}

@media (max-width: 991px) and (min-width: 768px) {
  .order {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;

  }

  .order-form {
    width: 48%;
    //float: right;
  }
}

@media (max-width: 767px) {
  .order {
    width: 420px;
    margin-left: auto;
    margin-right: auto;
  }

  .order .timer--mini {
    margin-left: auto;
    margin-right: auto;
  }

  .product-card-content:before {
    width: 294px;
    height: 346px;
  }

  .product-card .product .product-image {
    margin-left: 0;
    width: 280px;
  }

  .product-card .product .sale-text {
    font-size: 30px;
  }

  .product-card-promo {
    padding: 30px;
  }

  .product-card-promo .text {
    width: 59%;
  }

  .product-card {
    padding-top: 30px;
  }
}

@media (max-width: 767px) and  (min-width: 586px)  {
  //.order {
  //  padding-left: 100px;
  //  padding-right: 100px;
  //}
}



@media (max-width: 585px) {

  .product-card-promo {
    left: -15px;
    min-width: 100vw;
    width: 100vw;

    border-radius: 0;
  }

  .order {
    width: 100%;
    max-width: 360px;
    margin-left: auto;
    margin-right: auto;
  }

  .product-card-content:before {
    width: 84vw;
    right: -37vw;
    height: 92vw;

  }

  .product-card .product .eye-image {
    top: 0;
    width: 35vw;
    height: 35vw;

    z-index: 30;
  }

  .product-card .product .sale-text {
    top: 6vw;
    right: -8vw;

    width: 44vw;
    height: 22vw;
    line-height: 22vw;
  }

  .product-card-content:before {

  }

  .product-card .product .product-image {
    position: relative;
    top: 12vw;
    right: -8vw;
    margin-left: 0;
    width: 58vw;
    height: 76vw;
    margin-top: 15vw;

    z-index: 20;
  }

  .product-card-promo:before {
    right: -8%;
    width: 45vw;
  }

  .product-card-promo {
    padding: 25px 15px;

  }

  .product-card-promo .text {
    line-height: 1.5;
  }

  .order .prices {
    padding-left: 10px;
    padding-right: 10px;
  }

}

@media (max-width: 480px) {

  .product-card-content:before {
    background-image: url("../images/girl-3-400.png");
  }

  .order {
    padding-left: 10px;
    padding-right: 10px;
  }

}

