.privilege {
  position: relative;
  padding-top: 40px;
  padding-bottom: 20px;

  &:before {
    position: absolute;
    top: 100px;
    left: calc(50% - 1370px);
    content: "";

    width: 834px;
    height: 754px;

    background: url("../images/flower.png") center no-repeat;
    background-size: contain;

    z-index: 20;
  }

  &:after {
    position: absolute;
    top: 200px;
    right: -webkit-calc(50% - 1370px);
    right: calc(50% - 1340px);
    content: "";

    width: 834px;
    height: 754px;

    background: url("../images/flower.png") center no-repeat;
    background-size: contain;

    //transform: scale(-1,1);

    z-index: 20;
  }
}



.privilege-content-wrapper {
  position: relative;
  padding-top: 85px;
  padding-bottom: 130px;

  margin-top: 70px;

  background: linear-gradient(84.42deg, rgba(221, 70, 70, 0.8) 0%, rgba(250, 134, 134, 0.968883) 89.37%, #FF9292 100%);


  &:before {
    position: absolute;
    bottom: 0;
    left: calc(50% - 214px);
    content: "";

    width: 420px;
    height: 660px;

    background: url("../images/girl-2.png") center bottom no-repeat;
    background-size: contain;
  }
}

.privilege-content {
  position: relative;
  z-index: 50;

}

.privilege-content-inner {
  display: flex;
  justify-content: space-between;

}

.privilege-box {
  position: relative;
  padding: 80px 10px 20px;
  width: 31.6%;
  min-height: 300px;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 0 10px 10px rgba(0,0,0,0.1);

  margin-bottom: 70px;

  &:before {
    position: absolute;
    bottom: calc(100% - 50px);
    left: 0;
    content: "";
    width: 100%;
    height: 90px;

    background: url("../images/icon-6.png") center no-repeat;
    background-size: contain;
  }

  &.minuses:before {
    background: url("../images/icon-7.png") center no-repeat;
    background-size: contain;
  }

  h4 {
    font-weight: 500;
    text-align: center;
  }

  ol {
    text-align: center;
    padding-left: 0;

    li {
      font-size: 15px;
      //list-style-type: decimal;
      margin-bottom: 14px;
    }
  }
}

.results-content {
  position: relative;
  margin-top: -130px;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .results-item {
    margin-right: 20px;

    &:last-child {
      margin-right: 0;
    }
  }
}

.results-item {
  position: relative;
  width: 220px;

  &:nth-child(2) {
    margin-right: 230px;
  }

  img {
    display: block;
    width: 100%;
  }

