@media (max-width: 1536px) {

}

@media (max-width: 1365px) {

}

@media (max-width: 1199px) {

  .privilege:before {
    left: calc(50% - 790px);

    width: 434px;
    height: 354px;
  }


  .privilege:after {

    right: calc(50% - 830px);
    //content: "";
    width: 534px;
    height: 354px;
  }


  .privilege-box {
    margin-bottom: 30px;
  }

  .privilege-content-wrapper {
    padding-top: 75px;
  }

  .privilege-content-wrapper:before {
    left: calc(50% - 170px);
    width: 340px;
  }

  .privilege-content-wrapper {
    margin-top: 30px;
  }


  .results-content {
    margin-top: -100px;
  }
}

@media (max-width: 991px) {

  .privilege:before {
    left: calc(50% - 700px);
  }

  .privilege:after {
    right: calc(50% - 730px);
  }

  .privilege-box {
    width: 48%;
  }

  .results-item:nth-child(2) {
    margin-right: 20px;
  }

  .results-item {
    width: 170px;
  }

  .privilege-content-wrapper {
    padding-bottom: 90px;
  }

  .results-content {
    margin-top: 0;
  }

  .privilege-content-wrapper {
    padding-bottom: 10px;
  }

}

@media (max-width: 991px) and (min-width: 768px) {

  .privilege-content-wrapper:before {
    display: none;
    background: none;
  }

}

@media (max-width: 767px) {

  .privilege {
    padding-bottom: 30px;
  }

  .results-content {
    display: block;
  }

  .results-item {
    width: 100%;
  }

  .results-item:nth-child(2) {
    margin-right: 0;
  }

  .privilege-content-wrapper {
    padding-bottom: 200px;
  }

  .privilege-content-wrapper:before {
    left: calc(50% - 280px);

    width: 160px;
    background-position: left bottom;
  }

  .privilege:after {
    top: auto;
    bottom: 0;

    right: calc(50% - 540px);
  }

  .privilege:before {
    top: 170px;
    left: calc(50% - 600px);
  }
}

@media (max-width: 767px) and (min-width: 586px) {

  .privilege-box ol li {
    font-size: 14px;
  }

}

.slick-slide {
  outline: none;
}

@media (max-width: 767px) and (min-width: 481px) {
  .results-content {
    position: relative;
    left: -10px;
    width: calc(100% + 20px);
  }

  .results .slick-slide {
    margin: 0 10px;
  }
}


.results .slick-dots {
  margin-top: 15px;

  li button {
    background-color: #C4C4C4;
  }

  li.slick-active button {
    background-color: #FF5353;
  }
}

@media (max-width: 585px) {

  .privilege-content-inner {
    display: block;
  }

  .privilege-box {
    width: 100%;
  }

  .privilege-box {
    padding-top: 70px;
    margin-bottom: 60px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .privilege-content-wrapper {
    padding-bottom: 180px;

    &:before {
      left: 0;
      height: 200px;
      width: 124px;

      background-image: url("../images/girl-2-124.png");
    }
  }

  .privilege:before {
    top: -20px;
    left: -50px;
    width: 131px;
    background-image: url("../images/flawer-210.png");
  }

  .privilege:after {
    bottom: 30px;
    right: -60px;
    width: 210px;
    height: 210px;
    background-position: left bottom;
    background-image: url("../images/flawer-210.png");
  }

  .privilege-box {
    min-height: initial;
  }

}

@media (max-width: 480px) {

  .privilege {
    padding-bottom: 20px;
  }

  .results {
    padding-bottom: 30px;
  }

}

